@import "../../scss/config";

.Categories {
  margin-top: 16px;
  margin-bottom: 32px;

  @media screen and (max-width: $break_sm - 1px){
    margin-top: 6px;
  }

  &-Gallery {
    display: grid;
    height: calc(100vh - 64px - 16px - 8px - 16px);
    max-height: 1120px;
    grid-template-columns: 3.5fr 3.5fr 5fr;
    grid-auto-rows: 50%;
    grid-gap: 8px;

    @media screen and (max-width: $break_sm - 1px){
      height: calc(100vh - 48px - 6px - 8px - 8px);
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 33.33333%;
      grid-gap: 4px;
    }
  }

  &-GalleryItem {
    position: relative;

    &:nth-child(1) {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 2;

      .Categories-Text {
        color: $white;
      }
    }
    &:nth-child(2) {
      grid-column-start: 3;
      grid-column-end: 4;
      grid-row-start: 1;
      grid-row-end: 3;
    }
    &:nth-child(3) {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 2;
      grid-row-end: 3;
    }
    &:nth-child(4) {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 2;
      grid-row-end: 3;
    }

    @media screen and (max-width: $break_sm - 1px){
      &:nth-child(1) {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 2;
      }
      &:nth-child(2) {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 4;
      }
      &:nth-child(3) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 3;

        .Categories-Text {
          top: inherit;
          bottom: 6px;
        }
      }
      &:nth-child(4) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 3;
        grid-row-end: 4;

        .Categories-Text {
          top: inherit;
          bottom: 6px;
        }
      }
    }
  }

  &-GalleryImage {
    height: 100%;
    width: 100%;
  }

  &-Text {
    position: absolute;
    top: 16px;
    left: 20px;
    z-index: 10;
    color: $black;

    @media screen and (max-width: $break_sm - 1px){
      top: 6px;
      left: 12px;
      font-size: 12px;
      line-height: 22px;
    }
  }
}